function multipleGlobalFun1(){
	console.log("multipleGlobalFun.js模块有两个全局方法，这个是1个方法打印的字符串!");
}
function multipleGlobalFun2(){
	console.log("multipleGlobalFun.js模块有两个全局方法，这个是2个方法打印的字符串!");
};
define("multipleGlobalFun", (function (global) {
    return function () {
        var ret, fn;
       fn = function () { //暴露多个全局变量
                console.log(this);
                return {
                    multipleGlobalFun1: multipleGlobalFun1,
                    multipleGlobalFun2: multipleGlobalFun2
                }
            };
        ret = fn.apply(global, arguments);
        return ret || global.multipleGlobalFun1;
    };
}(this)));

